.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

:root{
  --primary-white : #fff;
  --primary-teal : #30879c;
  --primary-light-teal : #81a5ad;
  --primary-dark : rgba(255,255,255,0);
  --primary-hover-teal : #46c0df;
}
body {
  /* font-family: 'Teko', sans-serif; */
  font-family: 'Caveat', cursive;
}

/************* Navbar *************/
nav{
  z-index : 2;
  /* font-family: 'Teko', sans-serif; */
  font-family: 'Caveat', cursive;
}
.logo {
  width : 10rem;
}

/************ NavLinks ************/
nav a.nav-link{
  /* font-weight : 500; */
  letter-spacing : 0.0375rem;
  cursor : pointer;
}

nav ul li{
  text-transform : uppercase;
}

.navbar-nav > li > a{
  margin-right : 0.75rem;
  margin-left : 0.75rem;
  color : #fff !important;
  color : var(--primary-white) !important;
  border-top : 0.1875rem solid rgba(255,255,255,0);
  border-top : 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
  max-width : 17.25rem;
  padding : 0 !important;
}

@media(max-width: 768px){
  .navbar-brand{
    width: 7rem;
  }
}

.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
  border-top : 0.1875rem solid #30879c;
  border-top : 0.1875rem solid var(--primary-teal);
}

.navbar-nav > li > a:hover{
  border-top : 0.1875rem solid #30879c;
  border-top : 0.1875rem solid var(--primary-teal);
}

/******** Header ******/

.header-wrapper{
  position : relative;
  background : url(/portfolio/static/media/PortCover.013abf0e.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height : 90vh;
}

.main-info{
  display : flex;
  position: absolute;
  top : 0;
  left : 0;
  right : 0;
  bottom : 0;
  flex-direction: column;
  justify-content : center;
  align-items : center;
  text-align : center;
  font-family: 'Caveat', cursive;
  z-index : 1;
}

.main-info h1{
  color : #30879c;
  color : var(--primary-teal);
  text-transform : uppercase;
}

/******** Typed Text *******/
.typed-text {
  font-size : 2rem;
  color : #81a5ad;
  color : var(--primary-light-teal);
}

@media(max-width: 768px){
  .main-info h1{
    font-size: 1.5rem !important;
  }
  .typed-text{
    font-size : 1.3rem !important;
  }
}

.btn-main{
  display: flex;
}

.btn-main-offer{
  border : 1px solid #30879c;
  border : 1px solid var(--primary-teal);
  text-transform: uppercase;
  border-radius : 0;
  padding : 0.625rem 0.75rem 0.75rem 0.75rem;
  text-decoration: none;
  color : #fff;
  color : var(--primary-white);
  margin : 2rem;
}

.btn-main-offer:hover{
  text-decoration: none;
  background-color: rgba(56, 129, 151, 0.7);
  color : #fff;
  color : var(--primary-white);
  transition: .2s ease-in-out;
}

.tsparticles-canvas-el{
  position : absolute;
  height : 100vh;
  width : 100vw;
  z-index : 1;
}
