
  /****** Toggle *********/
  .t{
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border: 1pz solid #434e5a;
    background-color: white;
    position: fixed;
    top: 10px;
    right: 10px;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  
  .t-icon{
    width: 15px;
    height: 15px;
  }
  
  .t-button{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #434e5a;
    position: absolute;
    cursor: pointer;
  }